document.addEventListener('DOMContentLoaded',function(){ iniciarApp()})

function iniciarApp(){
  navFija();
  crearGaleria();
  scrollNaveg();
}
function navFija(){
  const barra=document.querySelector('.header');
  const lugar=document.querySelector('.festival');
  const body=document.querySelector('body');

  window.addEventListener('scroll', function(){

    if(lugar.getBoundingClientRect().top<0){
      barra.classList.add('fijo');
     body.classList.add('scroll-body');
    }else{
      barra.classList.remove('fijo');
      body.classList.remove('scroll-body');
    }
  });
}

function scrollNaveg() {
  const enlaces=document.querySelectorAll('.navegacion a');
  enlaces.forEach(enlace => {
    enlace.addEventListener('click',function(e){
      e.preventDefault();
     const parte= e.target.attributes.href.value;
     const parteScroll=document.querySelector(parte);
     parteScroll.scrollIntoView({behavior: 'smooth'});
    })
  });
};

function crearGaleria(){
  const galeria=document.querySelector('.galeria-imagenes');
  for(let i=1; i<13;i++){
      const imagen=document.createElement('PICTURE');
      imagen.innerHTML=` 
      <source srcset="build/img/thumb/${i}.avif" type="image/avif">
      <source srcset="build/img/thumb/${i}.webp" type="image/webp">
      <img loading="lazy" width="100%" height="auto" src="build/img/thumb/${i}.jpg" alt="imagen galeria">
      `;

      imagen.onclick=function(){
        mostarImagen(i);
      }
      galeria.appendChild(imagen);

      
  }
}


function mostarImagen(i){
   const imagen=document.createElement('PICTURE');
      imagen.innerHTML=` 
      <source srcset="build/img/grande/${i}.avif" type="image/avif">
      <source srcset="build/img/grande/${i}.webp" type="image/webp">
      <img loading="lazy" width="100%" height="auto" src="build/img/grande/${i}.jpg" alt="imagen galeria">;`
    
      //crear la fondo en pantalla ( la imagen)
      const overlay=document.createElement('DIV')
      overlay.appendChild(imagen);
      overlay.classList.add('overlay');

      //boton cerrar la ventana
      const cerrarFoto=document.createElement('P');
      cerrarFoto.textContent='X';
      cerrarFoto.onclick=function(){
        const body=document.querySelector('body');
      body.classList.remove('fijar-body')
        overlay.remove();
      };
      cerrarFoto.classList.add('boton');
      overlay.appendChild(cerrarFoto)
    
      //mostrar en pantalla, a√±adiendo al html
    const body=document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body')
}
  

